export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			isCard: true,
			cardCur: 0,
			swiperList: [{
				id: 0,
				type: 'image',
				url: 'https://isuxdesign-1251263993.file.myqcloud.com/upload/detail/DTb3l563tdj4oLwmdj6soz0oUN0WiIi6HTayN4oa8B3.jpg'
			}, {
				id: 1,
				type: 'image',
				url: 'https://isuxdesign-1251263993.file.myqcloud.com/upload/detail/IJMyc5AM3BV3KopTfIHaHF6nG56XGMJpcoZD9M5p2Gv.jpg',
			}, {
				id: 2,
				type: 'image',
				url: 'https://isuxdesign-1251263993.file.myqcloud.com/upload/detail/QlOpWqTDznuWIOABg5djTreijHQs6WzUzsVu0rBHghN.jpg'
			}, {
				id: 3,
				type: 'image',
				url: 'https://qzonestyle.gtimg.cn/qzone/qzactStatics/imgs/20171122191532_f2975b.jpg'
			}, {
				id: 4,
				type: 'image',
				url: 'https://cdn-isux.qq.com/upload/detail/f57Y2wEryDwjWmTr2BypYaCA6CgSKjknAJtGRIR4FcR.jpeg'
			}, {
				id: 5,
				type: 'image',
				url: 'https://isuxdesign-1251263993.file.myqcloud.com/upload/detail/2LtQ2KZEDOUFAcWfLEzL49EKXsDPVjeOv2NtsWFLbZP.jpg'
			}, {
				id: 6,
				type: 'image',
				url: 'https://isuxdesign-1251263993.file.myqcloud.com/upload/detail/UlS5cEY2AksTMvw9EWUDEx5p6yqFrhnj6oYrkpBcglk.jpg'
			}],
			dotStyle: false,
			towerStart: 0,
			direction: '',
			worksAndUser:[],
			newestApt:[],
			//推荐模特
			recomendModel:{},
			//推荐摄影师
			recomendPhotographer:{},
			
			
		};
	},
	onLoad() {
		//this.TowerSwiper('swiperList');
		// 初始化towerSwiper 传已有的数组名即可
	},
	onShow() {
		//获取最新约拍
		this.getNewestApt();
		
		
		//获取用户动态
		this.getWorksAndUser();
		
		//获取推荐模特
		this.getRecomendModel();
		
		//获取推荐摄影师
		this.getRecomendPhotographer();
	},
	methods: {
		IsCard(e) {
			this.isCard = e.detail.value
		},
		DotStyle(e) {
			this.dotStyle = e.detail.value
		},
		// cardSwiper
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		
		//获取最新约拍
		getNewestApt:function(){
			uni.request({
				url: this.createApiUrl('appointment/get/newest'),
				method: 'GET',
				data: {},
				success: res => {
					console.log(res);
					res = res.data.data;
					this.newestApt = res;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取用户动态
		getWorksAndUser:function(){
			uni.request({
				url: this.createApiUrl('works/get/all-user'),
				method: 'GET',
				data: {},
				success: res => {
					
					res = res.data.data;
					this.worksAndUser = res;
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取推荐模特
		getRecomendModel:function(){
			uni.request({
				url: this.createApiUrl('/user/get/all/2'),
				method: 'GET',
				success: res => {
					console.log(res);
					this.recomendModel = res.data.data;
					
					console.log(this.recomendModel);
				},
				fail: (err) => {
					console.log(err);
				},
				complete: () => {}
			});
		},
		
		//获取推荐摄影师
		getRecomendPhotographer:function(){
			uni.request({
				url: this.createApiUrl('/user/get/all/1'),
				method: 'GET',
				success: res => {
					console.log(res);
					this.recomendPhotographer = res.data.data;
					
					console.log(this.recomendPhotographer);
				},
				fail: (err) => {
					console.log(err);
				},
				complete: () => {}
			});
		},
		

		
	}
}
